import { Container, Row, Col, Button } from 'react-bootstrap';

const Footer = () => {
  return (
    <footer className="bg-light py-4  mt-4">
      <Container>
        <Row>
          <Col lg={4} md={6}>
            <h5>Enlaces útiles</h5>
            <ul className="list-unstyled">
              <li>
                <a href="/"><h5>Inicio</h5></a>
              </li>
              <li>
                <a href="/nosotros"><h5>Nosotros</h5></a>
              </li>
              <li>
                <a href="/contactos"><h5>Contactos</h5></a>
              </li>
            </ul>
          </Col>
          <Col lg={4} md={6}>
            <h5>Contacto</h5>
            <address>
              Dirección: 123 Calle Principal<br />
              Ciudad, La Paz - Bolivia<br />
              Teléfono:78881873
            </address>
          </Col>
          <Col lg={4}>
            <h5>Suscríbete</h5>
            <p>Suscríbete a nuestro boletín para recibir las últimas actualizaciones.</p>
            <div className="input-group mb-3">
              <input type="email" className="form-control" placeholder="Correo electrónico" aria-label="Correo electrónico" aria-describedby="button-addon2" />
              <Button variant="dark" id="button-addon2">
                Suscribirse
              </Button>
            </div>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col>
            <p className="text-center">© 2023 Lang. Carla Ines Quispe Gutierrez.</p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
};

export default Footer;
