import React from 'react';
import alImage2 from '../img/docentess.png';
import pd1 from '../img/pd1.jpg';
import pd2 from '../img/pd2.jpg';
import pd3 from '../img/pd3.jpg';
import pd4 from '../img/pd4.jpg';
import pd5 from '../img/pd5.jpg';
import pd6 from '../img/pd6.jpg';
import pd7 from '../img/pd7.jpg';
import pd8 from '../img/pd8.jpg';
import alImage1 from '../img/pp.jpeg';
import { useState } from 'react';

const Docentes = () => {
  const docentes = [
    {
      id: 1,
      nombre: 'Lic. Gualberto Quisbert',
      imagen: pd1,
      informacion: 'Docente de Ingles con mas de 10 años de experiencia',
    },
    {
      id: 2,
      nombre: 'Lic. Lidya Meneses',
      imagen: pd2,
      informacion: 'Docente de Frances',
    },
    {
        id: 3,
        nombre: 'Lic. Guido Cossio',
        imagen: pd3,
        informacion: 'Información breve sobre el Docente 2.',
      },
      {
        id: 4,
        nombre: 'Lic. Armando Rivera',
        imagen: pd4,
        informacion: 'Docente de Frances',
      },
      {
        id: 5,
        nombre: 'Lic. Miriam Santos',
        imagen: pd5,
        informacion: 'Docente de Frances',
      },
      {
        id: 6,
        nombre: 'Lic. Jose Ticona',
        imagen: pd6,
        informacion: 'Docente de Chino',
      },
      {
        id: 7,
        nombre: 'Lic. Adalid Chavez',
        imagen: pd7,
        informacion: 'Docente de Ingles',
      },
      {
        id: 8,
        nombre: 'Lic. Mario Ortega',
        imagen: pd8,
        informacion: 'Docente de Chino',
      },
    // ... y así sucesivamente con otros docentes
  ];

  const [activeDocente, setActiveDocente] = useState(null);

  const handleMouseEnter = (id) => {
    const docente = docentes.find((docente) => docente.id === id);
    setActiveDocente(docente);
  };

  const handleMouseLeave = () => {
    setActiveDocente(null);
  };

  return (
    <div>
         <div className="container mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        <div className="alert alert-warning text-center">
          <h1>Director de Lang</h1>
        </div>
        <div className="row">
          <div className="col-md-6">    
            <img src={alImage1} style={{ width: '600px', height: '500px' }} alt="Imagen" className="img-fluid " />
          </div>
          <div className="col-md-6">
            <div>
              <h2>Lic. Octavio Rodriguez</h2>
              <p>
                En una ciudad vibrante y multicultural, se erige el Instituto de Idiomas Lang, un centro educativo reconocido por su excelencia en la enseñanza de lenguas extranjeras. En la cúspide de este instituto se encuentra el Director, el Sr. Martín Reyes, un hombre apasionado por la lingüística y comprometido con la expansión del conocimiento a través de las fronteras del lenguaje.
              <br></br>

              <br></br>
              Después de graduarse con honores en Lingüística Aplicada, Martín se unió al Instituto de Idiomas Lang como profesor. Su pasión por enseñar y su habilidad para conectar con los estudiantes rápidamente lo llevaron a ser reconocido como uno de los mejores educadores en el campo de la enseñanza de idiomas.

                Con el tiempo, su dedicación y liderazgo no pasaron desapercibidos. Fue ascendido a Director del Instituto, asumiendo la responsabilidad de guiar el rumbo de la institución hacia la excelencia académica y la innovación pedagógica.
              <br></br>
              Bajo su liderazgo, el Instituto de Idiomas Lang se convirtió en un faro de aprendizaje y entendimiento intercultural. Los estudiantes florecieron no solo en el dominio de idiomas, sino también en la apertura mental y el entendimiento de la riqueza que la diversidad aporta a la sociedad.
              </p>
            </div>
          </div>
        </div>
        <div className="mt-5"></div>
      </div>
      <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(141, 73, 37, 0.8)' }}>
        <div className="row">
          <div className='col-md-1'></div>
          <div className="col-md-9">
            <img src={alImage2} alt="Imagen" style={{ width: '600px', height: '150px' }} className="img-fluid" />
          </div>
          <div className='col-md-2'></div>
        </div>
      </div>
      <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        <h2>Docentes</h2>
        <div className="row">
          {docentes.map((docente) => (
            <div key={docente.id} className="col-md-3 mt-4">
              <div
                onMouseEnter={() => handleMouseEnter(docente.id)}
                onMouseLeave={handleMouseLeave}
                style={{ position: 'relative' }}
              >
                {/* Imagen del docente */}
                {/* Ejemplo: */}
                <img src={docente.imagen} alt={`Imagen de ${docente.nombre}`} style={{ width: '100%', height: 'auto', borderRadius: '8px' }} className="img-fluid" />
                {/* Información del docente */}
                {activeDocente && activeDocente.id === docente.id && (
                  <div style={{ position: 'absolute', bottom: '-50px', left: '50%', transform: 'translateX(-50%)', width: '80%', backgroundColor: '#ffffff', borderRadius: '8px', padding: '10px', boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)', textAlign: 'center' }}>
                    <h4>{activeDocente.nombre}</h4>
                    <p>{activeDocente.informacion}</p>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Docentes;
