import { useState, useEffect } from 'react';
import CountUp from 'react-countup';
import logo from './log.png';
import alImage from '../img/cuatro.png';
import alImage1 from '../img/tres.png';
import alImage2 from '../img/siete.png';
import alImage3 from '../img/es.png';
import alImage4 from '../img/egre.png';
import alImage5 from '../img/docentes.png';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const Nosotros = () => {
    const [students, setStudents] = useState(0);
    const [graduates, setGraduates] = useState(0);
    const [teachers, setTeachers] = useState(0);

    useEffect(() => {
        setTimeout(() => {
            setStudents(500);
            setGraduates(300);
            setTeachers(30);
        }, 1000);
    }, []);

    return (
        <div>
            <div className="mt-4 p-5 text-white rounded" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)' }}>
                <div className="row">
                    <div className="col-md-6">
                        <img src={logo} style={{ width: '700px', height: '200px', marginLeft: '150px' }} className="rounded-pill" alt="Logo" />
                    </div>
                    <div className="col-md-6">
                        <img src={alImage} alt="Imagen" style={{ width: '400px', height: '160px' }} className="img-fluid waveEffect" />
                    </div>
                </div>
            </div>
            {/* Parte de la misión y visión */}
            <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
                <Row>
                    <Col md={6}>
                        <Card className="bg-outline-warning text-blue text-center card-animation-left">
                            <Card.Body>
                                <Card.Title>Misión</Card.Title>
                                <Card.Text>
                                    Nuestra misión es proporcionar educación lingüística de alta calidad, promoviendo la comprensión intercultural y el amor por los idiomas.
                                    <br />
                                    <img src={alImage2} alt="Imagen" style={{ width: '400px', height: '300px' }} className="img-fluid" />
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col md={6}>
                        <Card className="bg-outline-warning text-blue text-center card-animation-right">
                            <Card.Body>
                                <Card.Title>Visión</Card.Title>
                                <Card.Text>
                                    Nuestra visión es ser líderes en la enseñanza de idiomas, brindando herramientas para la comunicación global y la conexión entre culturas.
                                    <br />
                                    <img src={alImage1} alt="Imagen" style={{ width: '400px', height: '300px', marginRight:'-150'}} className="img-fluid" />
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </div>
            {/* Contadores */}
            <div className="container-fluid mt-4 text-center" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
                <Row className="justify-content-center">
                    <Col md={4}>
                        <h4>Estudiantes Inscritos</h4>
                        <img src={alImage3} alt="Imagen" style={{ width: '140px', height: '140px' }} className="img-fluid" />
                        <br></br>
                        <CountUp end={students} duration={5} />
                    </Col>
                    <Col md={4}>
                        <h4>Estudiantes Egresados</h4>
                        <img src={alImage4} alt="Imagen" style={{ width: '140px', height: '140px' }} className="img-fluid" />
                        <br></br>
                        <CountUp end={graduates} duration={5} />
                    </Col>
                    <Col md={4}>
                        <h4>Docentes</h4>
                        <img src={alImage5} alt="Imagen" style={{ width: '140px', height: '140px' }} className="img-fluid" />
                        <br></br>
                        <CountUp end={teachers} duration={5} />
                    </Col>
                </Row>
            </div>
            <div className="container-fluid mt-4 text-center" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
                <h2>Videos de YouTube</h2>
                <div className="row justify-content-center">
                    <div className="col-md-4 mb-4">
                    <iframe
                            width="100%"
                            height="200"
                            src="https://www.youtube.com/embed/EBAgQXDw9yA"
                            title="YouTube video player"
                            frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen
                        ></iframe>
                    </div>
                    <div className="col-md-4 mb-4">
                    <iframe
                            width="100%"
                            height="200"
                            src="https://www.youtube.com/embed/s7OfMG7ccPE"
                            title="YouTube video player"
                            frameBorder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen
                        ></iframe>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Nosotros;
