import React, { useState } from 'react';
import { Container, Button, Form, ProgressBar, Alert } from 'react-bootstrap';
import aImage from '../img/a.png';

const Home = () => {
  const [showTest, setShowTest] = useState(false);
  const [answers, setAnswers] = useState({
    answer1: 'true',
    answer2: 'true',
    answer3: 'false',
    answer4: 'true',
    answer5: 'false',
  });
  const [currentQuestion, setCurrentQuestion] = useState(1);
  const totalQuestions = 5;
  const [result, setResult] = useState(null);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setAnswers({ ...answers, [name]: value });
  };

  const handleNextQuestion = () => {
    if (currentQuestion < totalQuestions) {
      setCurrentQuestion(currentQuestion + 1);
    } else {
      calculateResult();
    }
  };

  const calculateResult = () => {
    let score = 0;
    // Aquí puedes calcular la calificación basada en las respuestas
    // Por ejemplo, sumar puntos por cada respuesta correcta
    // En este caso, es solo un ejemplo
    Object.values(answers).forEach((answer) => {
      if (answer === 'true') {
        score += 20; // Cada respuesta correcta suma 20 puntos
      }
    });
    setResult(score);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
  };

  const toggleTest = () => {
    setShowTest(!showTest);
  };

  return (
    <div>
      <div className="container mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        <div className="alert alert-warning text-center">
          <h1>Bienvenido a Lang</h1>
        </div>
        <div className="row">
          <div className="col-md-6">
            <img src={aImage} alt="Imagen" className="img-fluid waveEffect" />
          </div>
          <div className="col-md-6">
            <div>
              <h2>Lang</h2>
              <p>
              Hace 25 años, en una bulliciosa ciudad, nació un instituto de idiomas llamado "Lang". Su fundador, el apasionado lingüista y viajero empedernido, el Profesor García, soñaba con crear un espacio donde las barreras lingüísticas se desvanecieran, donde la diversidad cultural se celebrara y donde el mundo se conectara a través de los idiomas.
              <br></br>
              Con una visión clara y una determinación inquebrantable, el Profesor García reunió a un equipo de apasionados profesores multilingües que compartían su visión. Juntos, inauguraron Lingua en un pequeño edificio con aulas humildes pero llenas de entusiasmo y promesas.
              <br></br>
              Hoy, cuando Lang celebra su 25 aniversario, el Profesor García, con arrugas en el rostro pero con la misma chispa en los ojos, mira hacia atrás con gratitud y hacia adelante con emoción. Su legado no solo radica en el instituto que fundó, sino en las vidas que ha tocado y en el entendimiento que ha fomentado a lo largo de los años.
              <br></br>
              En cada aula de Lang, la historia de 25 años de pasión, dedicación y amor por los idiomas sigue viva. Y mientras el mundo sigue cambiando, Lingua sigue siendo un faro para aquellos que buscan comprender, comunicarse y conectarse en un mundo cada vez más interconectado.
              </p>
            </div>
          </div>
        </div>
        <div className="mt-5"></div>
      </div>
      <div className="container mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        {/* Botón con imagen para mostrar/ocultar el formulario */}
        <div className="text-center">
          <Button variant="warning" onClick={toggleTest}>
            <h2>Test de Inglés</h2>
          </Button>
        </div>
        {/* Test de Inglés */}
        {showTest && (
          <Container className="mt-4">
            <h3>Test de Inglés</h3>
            <Form onSubmit={handleSubmit}>
              {/* Mostrar la pregunta actual */}
              {currentQuestion <= totalQuestions && (
                <>
                  <Form.Group controlId={`question${currentQuestion}`}>
                    <Form.Label>{`Pregunta ${currentQuestion}: Hola en Ingles es Hello`}</Form.Label>
                    <div>
                      <Form.Check
                        inline
                        label="Verdadero"
                        type="radio"
                        name={`answer${currentQuestion}`}
                        value="true"
                        onChange={handleInputChange}
                      />
                      <Form.Check
                        inline
                        label="Falso"
                        type="radio"
                        name={`answer${currentQuestion}`}
                        value="false"
                        onChange={handleInputChange}
                      />
                    </div>
                    <br></br>
                  </Form.Group>
                  <ProgressBar now={(currentQuestion / totalQuestions) * 100} label={`${currentQuestion}/${totalQuestions}`} />
                  <br></br>
                  <Button variant="primary" onClick={handleNextQuestion}>
                    {currentQuestion === totalQuestions ? 'Finalizar Test' : 'Siguiente'}
                  </Button>
                  <br></br>
                </>
              )}
              {result !== null && (
                <>
                <br></br>
                  <Alert variant="warning">¡Test completado! Resultado:</Alert>
                  <p>Calificación: {result}</p>
                </>
              )}
            </Form>
          </Container>
        )}
      </div>
    </div>
  );
};

export default Home;
