import React from 'react';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Table from 'react-bootstrap/Table';
import Carousel from 'react-bootstrap/Carousel';
import alImage1 from '../img/cch.png';
import alImage2 from '../img/chino.png';
import alImage3 from '../img/inter.png';
import alImage4 from '../img/basico.png';
import alImage5 from '../img/avan.png';
import alImage6 from '../img/ingless.png';
import book1 from '../img/lc.jpg';
import book2 from '../img/lc4.jpg';
import book3 from '../img/lc3.jpg';
import alImage10 from '../img/m.png';
import alImage11 from '../img/nueve.png';
import book4 from '../img/p1.jpg';
import book5 from '../img/p2.jpg';
import book6 from '../img/p3.jpg';
import res from '../img/re.png';
const Chino =()=>{
    const contenidoBasico = [
        { tema: 'Gramática básica', objetivo: 'Aprender estructuras gramaticales ' },
        { tema: 'Vocabulario esencial', objetivo: 'Palabras clave para situaciones' },
        // Agrega más contenido según sea necesario para el nivel básico
    ];

    const contenidoIntermedio = [
        { tema: 'Comprensión lectora', objetivo: 'Comprender textos más complejos' },
        { tema: 'Conversación fluida', objetivo: 'Mejorar habilidades de expresión oral' },
        // Agrega más contenido según sea necesario para el nivel intermedio
    ];

    const contenidoAvanzado = [
        { tema: 'Redacción avanzada', objetivo: 'Escribir textos sofisticados y coherentes' },
        { tema: 'Traducción profesional', objetivo: 'Capacidad para traducir textos complejos' },
        // Agrega más contenido según sea necesario para el nivel avanzado
    ];
    return (
        <div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(112, 66, 20, 0.7)'}}>
            <div className="row">
                <div className='col-md-2'></div>
                <div className="col-md-4">
                    <img src={alImage2} alt="Imagen" style={{ width: '600px', height: '250px' }} className="img-fluid" />
                </div>
                <div className="col-md-6">
                    <img src={alImage1} alt="Imagen" style={{ width: '600px', height: '250px', animation: 'tambaleo 8s ease-in-out infinite' }} className="img-fluid" />
                </div>
            </div>
        </div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
            <h2>Contenidos</h2>
            <Row className="justify-content-around">
                <Col md={3}  className="card-animation">
                    <Card style={{ backgroundColor: 'rgba(112, 66, 20, 0.7)', color: 'white' }}>
                        <Card.Body>
                        <div className="d-flex align-items-center">
                                <img src={alImage4} alt="Icono" style={{ width: '150px', height: '150px', borderRadius: '50%' }} />
                                <div style={{ marginLeft: '20px' }}>
                                    <Card.Title><h2>Básico</h2></Card.Title>
                                    <Card.Text>
                                        {/* Tabla de contenido básico */}
                                        <Table bordered hover variant="light">
                                            <thead>
                                                <tr>
                                                    <th>Tema</th>
                                                    <th>Objetivo</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {contenidoBasico.map((item, index) => (
                                                    <tr key={index}>
                                                        <td>{item.tema}</td>
                                                        <td>{item.objetivo}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </Table>
                                    </Card.Text>
                                </div>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={3}  className="card-animation">
                    <Card style={{ backgroundColor: 'rgba(112, 66, 20, 0.7)', color: 'white' }}>
                        <Card.Body>
                            <div className="d-flex align-items-center">
                                <img src={alImage3} alt="Icono" style={{ width: '150px', height: '150px', borderRadius: '50%' }} />
                                <div style={{ marginLeft: '20px' }}>
                                <Card.Title><h2>Intermedio</h2></Card.Title>
                                <Card.Text>
                                        {/* Tabla de contenido básico */}
                                        <Table bordered hover variant="light">
                                            <thead>
                                                <tr>
                                                    <th>Tema</th>
                                                    <th>Objetivo</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {contenidoIntermedio.map((item, index) => (
                                                    <tr key={index}>
                                                        <td>{item.tema}</td>
                                                        <td>{item.objetivo}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </Table>
                                    </Card.Text>
                                </div>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={3}  className="card-animation">
                    <Card style={{ backgroundColor: 'rgba(112, 66, 20, 0.7)', color: 'white' }}>
                        <Card.Body>
                        <div className="d-flex align-items-center">
                            <img src={alImage5} alt="Icono" style={{ width: '150px', height: '150px', borderRadius: '50%' }} />
                            <div style={{ marginLeft: '20px' }}>
                                <Card.Title><h2>Avanzado</h2></Card.Title>
                                <Card.Text>
                                        <Table bordered hover variant="light">
                                            <thead>
                                                <tr>
                                                    <th>Tema</th>
                                                    <th>Objetivo</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {contenidoAvanzado.map((item, index) => (
                                                    <tr key={index}>
                                                        <td>{item.tema}</td>
                                                        <td>{item.objetivo}</td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </Table>
                                    </Card.Text>
                            </div>
                        </div>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(112, 66, 20, 0.7)'}}>
            <div className="row">
                <div className='col-md-2'></div>
                <div className="col-md-6">
                    <img src={alImage10} alt="Imagen" style={{ width: '600px', height: '250px' }} className="img-fluid" />
                </div>
                <div className="col-md-4">
                    <img src={alImage11} alt="Imagen" style={{ width: '400px', height: '250px', animation: 'balancingEffect 4s infinite' }} className="img-fluid" />
                </div>
            </div>
        </div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={book1}
                        alt="First slide"
                        style={{ height: '600px', objectFit: 'contain' }}
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={book2}
                        alt="Second slide"
                        style={{ height: '400px', objectFit: 'contain' }}
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={book3}
                        alt="Third slide"
                        style={{ height: '400px', objectFit: 'contain' }}
                    />
                </Carousel.Item>
            </Carousel>
        </div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(112, 66, 20, 0.7)'}}>
            <div className="row">
                <div className='col-md-2'></div>
                <div className="col-md-6">
                    <img src={res} alt="Imagen" style={{ width: '600px', height: '150px' }} className="img-fluid" />
                </div>
            </div>
        </div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        <Row className="justify-content-center mt-4">
        <Col md={3}>
        <Card style={{ width: '18rem' }}>
            <img src={book4} className="card-img-top" alt="User Avatar" />
            <Card.Body>
            <h5 className="card-title">Maria Quisbert</h5>
            <p className="card-text">Es bueno decir que la experiencia que me llevo de los cursos es muy grata debido a ...</p>
            <a href="#" className="btn btn-warning">
            <h5>Ver mas...</h5>
            </a>
            </Card.Body>
        </Card>
        </Col>
        <Col md={3}>
        <Card style={{ width: '18rem' }}>
            <img src={book5} className="card-img-top" alt="User Avatar" />
            <Card.Body>
            <h5 className="card-title">Juan Carlos Gutierrez</h5>
            <p className="card-text">Con los cursos y el material que se me facilito , estoy muy complacido por que tengo un buen nivel de ingles</p>
            <a href="#" className="btn btn-warning">
                <h5>Ver mas...</h5>
            </a>
            </Card.Body>
        </Card>
        </Col>
        <Col md={3}>
        <Card style={{ width: '18rem' }}>
            <img src={book6} className="card-img-top" alt="User Avatar" />
            <Card.Body>
            <h5 className="card-title">Lia Perez</h5>
            <p className="card-text">Me gusta mucha el plan de del idioma de ingles , gracias los materiales y videos tengo mayor conocimiento</p>
            <a href="#" className="btn btn-warning">
            <h5>Ver mas...</h5>
            </a>
            </Card.Body>
        </Card>
        </Col>
    </Row>
        </div>
    </div>
    )
}
export default Chino;