import alImage2 from '../img/sucursal.png';
import ubi1 from '../img/ubi.jpeg';
import ubi2 from '../img/ubi2.jpeg';
import ubi3 from '../img/ubi3.jpg';
import ico1 from '../img/ubii.png';
import ico2 from '../img/tel.png';
import ico3 from '../img/men.png';
import React, { useState } from 'react';

const Contactos =()=>{
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: ''
      });
    
      const handleChange = e => {
        const { name, value } = e.target;
        setFormData({
          ...formData,
          [name]: value
        });
      };
    
      const handleSubmit = e => {
        e.preventDefault();
        // Aquí puedes implementar la lógica para enviar el formulario
        // Por ejemplo, puedes usar formData para enviar los datos al servidor
    
        // Limpiar el formulario después de enviar
        setFormData({
          name: '',
          email: '',
          message: ''
        });
    
        // Mostrar un alert al enviar el formulario
        alert('¡Mensaje enviado correctamente!');
      };
    return (
        <div>
             <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(141, 73, 37, 0.8)' }}>
                <div className="row">
                    <div className='col-md-1'></div>
                    <div className="col-md-11">
                        <img src={alImage2} alt="Imagen" style={{ width: '600px', height: '150px' }} className="img-fluid" />
                    </div>
                </div>
            </div>
        <div className="container mt-4 alert alert-warning" >
          <h1>Ciudad de La Paz</h1>
        </div>
        <div className="container mt-4 fade-in" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' , textAlign: 'center'}}>
        <div className="row">
          <div className="col-md-4"  style={{ borderRight: '1px solid #ccc', textAlign: 'center' }}>
            <h4>Ubicación</h4>
            <img src={ico1} alt="Imagen del lugar" style={{ maxWidth: '50%', height: 'auto' }} />
            <p>Avenida Mariscal Santa Cruz, Edificio Hansa, Piso 20.</p>
          </div>
          <div className="col-md-4" style={{ borderRight: '1px solid #ccc', textAlign: 'center'}}>
            <h4>Contacto</h4>
            <img src={ico2} alt="Imagen del lugar" style={{ maxWidth: '10%', height: 'auto' }} /> <span><h4>  70545891 - 2462984 </h4></span>
            <br></br>
            <img src={ico3} alt="Imagen del lugar" style={{ maxWidth: '10%', height: 'auto' }} /><span><h4> Lapaz.lang@gmail.com</h4></span>
          </div>
          <div className="col-md-4">
            <h4>Imagen del Lugar</h4>
            <img src={ubi1} alt="Imagen del lugar" style={{ maxWidth: '100%', height: 'auto' }} />
          </div>
        </div>
      </div>
      <div className="container mt-4 alert alert-warning" >
          <h1>Ciudad de El Alto</h1>
        </div>
      <div className="container mt-4 fade-in" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' , textAlign: 'center'}}>
        <div className="row">
          <div className="col-md-4"  style={{ borderRight: '1px solid #ccc', textAlign: 'center' }}>
            <h4>Ubicación</h4>
            <img src={ico1} alt="Imagen del lugar" style={{ maxWidth: '50%', height: 'auto' }} />
            <p>Av. Mariscal Santa Cruz. Edif. San Pablo Piso 4</p>
          </div>
          <div className="col-md-4" style={{ borderRight: '1px solid #ccc', textAlign: 'center'}}>
            <h4>Contacto</h4>
            <img src={ico2} alt="Imagen del lugar" style={{ maxWidth: '10%', height: 'auto' }} /> <span><h4>  70545891 - 2462984 </h4></span>
            <br></br>
            <img src={ico3} alt="Imagen del lugar" style={{ maxWidth: '10%', height: 'auto' }} /><span><h4> Lapaz.lang@gmail.com</h4></span>
          </div>
          <div className="col-md-4">
            <h4>Imagen del Lugar</h4>
            <img src={ubi2} alt="Imagen del lugar" style={{ maxWidth: '100%', height: 'auto' }} />
          </div>
        </div>
      </div>
      <div className="container mt-4 alert alert-warning" >
          <h1>Satelite</h1>
        </div>
      <div className="container mt-4 fade-in" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' , textAlign: 'center'}}>
        <div className="row">
          <div className="col-md-4"  style={{ borderRight: '1px solid #ccc', textAlign: 'center' }}>
            <h4>Ubicación</h4>
            <img src={ico1} alt="Imagen del lugar" style={{ maxWidth: '50%', height: 'auto' }} />
            <p>Calle. Belisario Salinas N° 327, Edificio:"CLAUDIA".Primer Piso Of. 102</p>
          </div>
          <div className="col-md-4" style={{ borderRight: '1px solid #ccc', textAlign: 'center'}}>
            <h4>Contacto</h4>
            <img src={ico2} alt="Imagen del lugar" style={{ maxWidth: '10%', height: 'auto' }} /> <span><h4>  70545891 - 2462984 </h4></span>
            <br></br>
            <img src={ico3} alt="Imagen del lugar" style={{ maxWidth: '10%', height: 'auto' }} /><span><h4> Lapaz.lang@gmail.com</h4></span>
          </div>
          <div className="col-md-4">
            <h4>Imagen del Lugar</h4>
            <img src={ubi3} alt="Imagen del lugar" style={{ maxWidth: '100%', height: 'auto' }} />
          </div>
        </div>
      </div>
     <div className="container mt-4 fade-in" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px'}}>
         {/* Formulario al final de la página */}
         <h2>Contactanos</h2>
      <div className="container mt-4">
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="name"><h5>Nombre:</h5></label>
            <input
              type="text"
              className="form-control"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="email"><h5>Correo electrónico:</h5></label>
            <input
              type="email"
              className="form-control"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="message"><h5>Mensaje:</h5></label>
            <textarea
              className="form-control"
              id="message"
              name="message"
              value={formData.message}
              onChange={handleChange}
              required
            ></textarea>
          </div>
          <br></br>
          <button type="submit" className="btn btn-warning">
            <h5>Enviar</h5>
          </button>
        </form>
      </div>
     </div>
        </div>
    )
}
export default Contactos;