import { Outlet, Link } from "react-router-dom";
import logo from './log.png'; // Asegúrate de tener la ruta correcta para tu logo
import 'bootstrap/dist/css/bootstrap.min.css';
import { Nav, Navbar, NavDropdown } from 'react-bootstrap'; // Importa los componentes de Bootstrap

const NavBar = () => {
  return (
    <div>
      <Navbar bg="light" expand="sm">
        <div className="container">
          <Navbar.Brand href="/">
            <img src={logo} style={{ width: '200px', height: '80px'}} className="rounded-pill" alt="Logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
              <Nav.Link as={Link} to="/">
                <h4>Inicio</h4>
              </Nav.Link>
              <Nav.Link as={Link} to="/nosotros">
                <h4>Nosotros</h4>
              </Nav.Link>
              <NavDropdown title={<h4>Idiomas</h4>} id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to="/ingles">Ingles</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/frances">Frances</NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/chino">Chino</NavDropdown.Item>
              </NavDropdown>
              <Nav.Link as={Link} to="/docentes">
                <h4>Docentes</h4>
              </Nav.Link>
              <Nav.Link as={Link} to="/premios">
                <h4>Premios y Reconocimientos</h4>
              </Nav.Link>
              <Nav.Link as={Link} to="/contactos">
                <h4>Contactos</h4>
              </Nav.Link>
              <Nav.Link as={Link} to="/creditos">
                <h4>Creditos</h4>
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </div>
      </Navbar>
      <Outlet />
    </div>
  );
};

export default NavBar;
