import React from 'react';
import { Carousel, Image, Row, Col, Card } from 'react-bootstrap';
import prem from '../img/premios.png';
import prem1 from '../img/p11.jpg';
import prem2 from '../img/premio2.jpg';
import prem3 from '../img/p111.jpg';
const Premios = () => {
  const premios = [
    { id: 1, src: prem1 },
    { id: 2, src: prem2 },
    { id: 3, src: prem3 },
  ];

  // Lista de videos
  const videos = [
    'https://www.youtube.com/embed/uMpo7bq65N0',
  'https://www.youtube.com/embed/3UNwvNP-YTs',
  'https://www.youtube.com/embed/qMmkoC-jG_M',
  ];


  return (
    <div>
        <div className="container-fluid mt-4" style={{ backgroundColor: 'rgba(141, 73, 37, 0.8)' }}>
                <div className="row">
                    <div className='col-md-2'></div>
                    <div className="col-md-10">
                        <img src={prem} alt="Imagen" style={{ width: '300px', height: '150px' }} className="img-fluid" />
                    </div>
                </div>
            </div>
    <div>
      <div className="container mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
      <Carousel>
        {premios.map((premio) => (
          <Carousel.Item key={premio.id}>
            <Image src={premio.src} alt={`Premio ${premio.id}`} className="d-block mx-auto" 
              style={{ maxWidth: '700px', maxHeight: '1000px' }} fluid />
          </Carousel.Item>
        ))}
      </Carousel>
      </div>

      <div className="container mt-4" style={{ backgroundColor: 'rgba(255, 255, 255, 0.9)', padding: '20px' }}>
        <h1>Videos de Premiaciones</h1>
        <Row className="mt-4">
        {videos.map((video, index) => (
          <Col key={index} md={4} className="mb-4">
            <Card>
              <Card.Body>
                <iframe
                  width="100%"
                  height="200"
                  src={video}
                  title={`Video ${index + 1}`}
                  frameBorder="0"
                  allowFullScreen
                ></iframe>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
      </div>
    </div>
  
    </div>
  );
};

export default Premios;
